/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

.header {
  padding: 16px 4%;
  background: $aksen1;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  transition: all .3s ease;
  z-index: 100;
  &.fixed {
    padding-top: 8px;
    padding-bottom: 8px;
    // top: -300px
    .header-logo img {
      max-height: 50px; } }
  &.fixed.sticky {
    top: 0; }
  &-logo img {
    max-height: 70px; }
  &-wrap {
    position: relative; }
  &-nav {
    display: flex;
    height: 100%;
    &>ul {
      display: flex;
      align-items: center;
      padding: 0;
      margin: 0;
      justify-content: flex-end;
      width: 100%;
      // position: relative
      &>li {
        padding: 0 24px;
        &>a {
          padding: 0 0 4px;
          text-transform: uppercase;
          font-size: 15px;
          font-weight: 700;
          color: $text;
          text-decoration: none;
          transition: all .3s;
          position: relative; }
        &:hover,
        &.current-menu {
          &>a {
            color: $secondary;
            border-bottom: 2px solid $secondary;
            &:after {
              transform: scaleY(-1); } } } } } }
  &-right {
    height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    & > ul {
      padding: 0;
      margin: 0;
      display: flex;
      justify-content: flex-end;
      width: 100%;
      position: relative;
      & > li {
        padding: 0 12px;
        text-align: center;
        &.has-child {
          a {
            &:after {
              display: none; } } }
        & > a {
          display: inline-block;
          width: 14px;
          height: 14px;
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center; } } }
    .sub-menu {
      max-width: 75% !important;
      padding: 20px !important; }
    .dd-user {
      list-style: none;
      text-align: left;
      li {
        text-align: left;
        display: block;
        width: 100%;
        padding: 0;
        padding-left: 0px !important;
        &:before {
          display: inline-block;
          vertical-align: middle;
          width: 13px !important;
          height: 13px !important;
          margin-right: 10px;
          border: none !important;
          position: relative !important;
          top: auto !important;
          left: auto !important;
          @include bgcontain; }
        &.login {
          &:before {
            background-image: url(../images/sign-in-alt-solid.svg); } }
        &.create {
          &:before {
            background-image: url(../images/user-solid.svg); } } } } }

  .has-child {
    &> a {
      &:after {
        content: "";
        position: absolute;
        top: 3px;
        right: -25px;
        background-image: url(../images/chevron-down-solid.svg);
        background-size: contain;
        background-repeat: no-repeat;
        width: 15px;
        height: 15px; } }
    &:hover {
      .sub-menu {
        transform: scaleY(1);
        overflow: visible;
        visibility: visible; } } }
  .sub-menu,
  .subm-right {
    padding: 35px;
    background: #ffffff;
    width: 95%;
    position: absolute;
    top: 95%;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 20;
    box-shadow: 0 0 30px 0 rgba(0,0,0,0.15);
    transition: all 0.4s ease-in-out;
    visibility: hidden;
    overflow: hidden;
    transform-origin: 0 0;
    transform: scaleY(0);
    .head-sm {
      font-size: 15px;
      font-weight: 600;
      text-transform: uppercase;
      margin-bottom: 0;
      letter-spacing: 0.05em;
      margin-bottom: 10px;
      display: block; }
    ul {
      padding: 0;
      margin: 0; }
    li {
      display: block;
      padding-left: 18px;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 16px;
        width: 7px;
        height: 7px;
        border-radius: 50%;
        border: 1px solid #4c834a; }
      a {
        display: inline-block;
        padding: 8px 0;
        font-size: 14px;
        line-height: 19px;
        color: #151413;
        text-decoration: none; }
      &:hover a {
        color: #4c834a; } }

    .box-img {
      width: 100%;
      position: relative;
      overflow: hidden;
      .img {
        width: 100%;
        position: relative;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        transition: all .3s;
        &:before {
          content: "";
          padding: 58.8% 0;
          display: block; } }
      &:hover {
        .img {
          transform: scale(1.1); } } } }
  .cart {
    & > a {
      position: relative;
      & > span {
        opacity: 0;
        visibility: hidden;
        width: 16px;
        height: 16px;
        display: flex;
        text-align: center;
        border-radius: 50%;
        background-color: $primary;
        color: #ffffff;
        justify-content: center;
        align-items: center;
        font-size: 8px;
        line-height: 8px;
        position: absolute;
        top: -10px;
        right: -10px; } }
    &-filled {
      & > a > span {
        opacity: 1;
        visibility: visible; } }
    .subm-cart {
      max-width: 350px !important;
      width: 350px !important;
      margin: auto;
      left: auto;
      right: 30%; }
    &-cm-wrapper {
      border-bottom: 1px solid $border;
      margin-bottom: 10px;
      max-height: 180px;
      overflow-y: auto;
      padding-right: 3px; }
    &-cm-item {
      padding-bottom: 10px;
      margin-bottom: 10px;
      border-bottom: 1px solid $border;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-bottom: 15px;
      position: relative;
      &:last-child {
        margin-bottom: 0;
        border: none; }
      .img-box {
        width: 70px;
        height: 70px;
        border: 1px solid $border;
        border-radius: 6px;
        @include bgcover;
        margin-right: 10px; }
      .text-box {
        width: calc(100% - 125px);
        text-align: left; }
      h4 {
        font-size: 15px;
        line-height: 1em;
        margin-bottom: 0;
        font-weight: 600; }
      .detdesc {
        font-size: 12px;
        font-weight: 600;
        display: block; }
      .price {
        text-align: left;
        font-size: 14px;
        font-weight: 600;
        display: block; }
      .close {
        width: 13px;
        height: 13px;
        position: absolute;
        top: 0;
        right: 0;
        color: $text;
        .svg {
          width: 13px;
          height: 13px; }
        &:hover {
          color: $secondary; } } }
    .summary {
      font-size: 18px;
      line-height: 1.5em;
      padding-bottom: 10px;
      font-weight: 700; }
    .btn-primary {
      margin-right: 15px;
      &:last-child {
        margin-right: 0; } } }




  .burger-icon {
    position: absolute;
    top: 16px;
    right: 0;
    z-index: 251;
    width: 30px;
    padding-top: 32px;
    cursor: pointer;
    transition: all .5s;
    display: none;
    float: right;
    span {
      position: absolute;
      right: 0;
      width: 100%;
      height: 2px;
      background-color: $text;
      transition: opacity .5s .5s, top .5s .4s, transform .5s;
      transform: rotate(0deg);
      &:nth-child(1) {
        top: 0; }
      &:nth-child(2) {
        top: 8px;
        width: 75%; }
      &:nth-child(3) {
        top: 16px;
        width: 90%; } }
    &:after {
      content: "";
      font-size: 10px;
      line-height: 16px;
      font-style: normal;
      position: relative;
      display: block;
      width: 100%;
      text-align: center;
      color: #ffffff; }
    &:hover {
      cursor: pointer; } }
  .container-fluid > .row > .col-lg-8 {
    position: inherit !important; }
  @include responsive(1200px) {
    padding-left: 0;
    padding-right: 0;
    &-nav {
      &> ul > li {
        padding: 0 16px; } } }
  @include maxtablet {
    &-nav {
      // display: none
      position: fixed;
      top: 0;
      left: 0;
      width: 265px;
      height: 100vh;
      overflow-y: auto;
      background: #FFF;
      align-items: start;
      transform: translateX(-500px);
      transition: all .3s ease;
      z-index: 10;
      &>ul {
        height: 100%;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        padding-top: 30px;
        &>li {
          width: 100%;
          margin-bottom: 8px;
          max-height: 24px;
          &.has-child {
            &:hover {
              max-height: 999999px;
              .sub-menu {
                max-height: 999999px; } } } } } }
    &-right {
      padding-right: 30px;
      .sub-menu {
        width: 180px !important;
        max-width: 180px !important; } }
    .sub-menu {
      position: relative;
      top: auto;
      left: auto;
      right: 0;
      max-height: 0;
      overflow: hidden;
      box-shadow: none;
      padding: 15px;
      .box-img {
        margin-bottom: 24px; } }
    .burger-icon {
      display: block; }
    .sub-menu,
    .subm-right {
      position: absolute !important; }
    .has-child {
      &:hover {
        .sub-menu,
        .subm-right {
          max-height: 99999px !important; } } } }
  @include responsive(1500px, 0) {
    &-right {
      .sub-menu {
        max-width: 95% !important; } } }
  @include mobile {
    &-logo {
      img {
        max-height: 55px; } }
    &-right {
      margin-top: -25px; }

    .burger-icon {
      top: -10px; }
    .cart {
      .subm-cart {
        max-width: 290px !important;
        width: 290px !important;
        left: 0;
        right: 0;
        margin: auto; } } } }


.menu-open {
  &:after {
    content: "";
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: #000000;
    opacity: .3;
    display: block;
    z-index: 5; }
  .burger-icon {
    span {
      &:nth-child(1) {
        top: 0;
        width: 95%; }
      &:nth-child(2) {
        top: 8px;
        width: 100%; }
      &:nth-child(3) {
        top: 16px;
        width: 75%; } } }
  .header {
    &-logo {
      // z-index: 300
      position: relative; }
    &-nav {
      display: block;
      transform: translateX(0); }
    .sub-menu {
      max-height: 999999px; } } }
