        margin-bottom: 15px;
        text-transform: uppercase; }
      h3 {
        font-size: 75px;
        line-height: 1.1em;
        margin-bottom: 15px;
        text-transform: uppercase; }
      .btn-wline {
        display: inline-block;
        margin: 0;
        text-decoration: none;
        vertical-align: middle;
        cursor: pointer;
        white-space: nowrap;
        // transition: all 0.4s ease-in-out
