        background: rgba(0,0,0,0);
        color: #000000;
        position: relative;
        font-weight: 600;
        &:before {
          content: "";
          display: inline-block;
          background: #030d1d;
          vertical-align: middle;
          width: 320px;
          height: 1px;
          margin-right: 12px;
 }          // transition: all 0.4s ease-in-out
