            font-size: 0.65714em; } } } }
    &-shipping {
      .bcs-item {
        flex-wrap: wrap;
        a,
        span {
          flex: 0 0 50%;
          max-width: 50%;
          display: inline-block;
          margin-bottom: 10px; }
        span {
          order: 0; }
        a {
          order: 1;
          text-align: right; }
        p {
          order: 2;
          flex: 0 0 100%;
          max-width: 100%; } } }
    .container {
      position: relative; }
    &-header {
      text-align: center; }
    .fg-heading {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      p {
        order: 0;
        margin-bottom: 10px; }
      label {
        order: 1; } }
    .header-logo {
      display: inline-block;
      margin: 0 auto; }
    &-l {
      padding-right: 0;
      position: inherit;
      .form-group {
        .col-sm-6 {
          margin-bottom: 15px; } }
      .form-action {
        width: calc(100% - 30px);
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        margin: 0 auto 15px;
        z-index: 40;
        .btn {
          font-size: 13px !important;
          &.btn-primary {
            min-width: 100px; } } } }
    &-r {
      padding-left: 0;
      padding-top: 0;
      margin-top: 40px;
      padding: 40px 0 100px;
      position: relative;
      z-index: 1;
      &:after {
        content: "";
        position: absolute;
        display: block;
        width: calc(100% + 30px);
        border-left: 0;
        height: 100%;
        bottom: 0;
        left: -15px;
        z-index: -1;
        background-color: #fafafa;
        border-top: 1px solid $border; } }
    &:after {
      display: none; } }
  @include responsive(340px) {
    &-l {
      .form-action {
        .btn.btn-primary {
          padding: 8px; } } } } }

